html {
  height: 100%;
  width: 100%;
}

body {
  min-height: 100%;
  min-width: 100%;
}

app-root {
  width: 100%;
  align-items: center;
  align-content: flex-start;
}

.loading-page {
  height: 100%;
  width: 100%;
  /* position: absolute; */
  background: #f7f7f7;
  display: grid;
  align-content: center;
  justify-content: center;
  justify-items: center;
}

body {
  transform-origin: center;
  display: flex;
}

.box {
  display: flex;
  --strip-size: 100px;
  width: 250px;
  aspect-ratio: 1;
  transform-origin: center;
  perspective: 1000px;
  animation-iteration-count: infinite;
  align-items: center;
  justify-content: center;
}

.box1 {
  position: absolute;
  --strip-size: 400px;
  width: 200px;
  aspect-ratio: 1.5;
  transform: rotate3d(1, 0, 0.6, 180deg);
  perspective: 1000px;
}

.box2 {
  position: absolute;
  --strip-size: 400px;
  width: 200px;
  aspect-ratio: 1.5;
  transform: rotate3d(1, -0.5, 0, 63deg);
  perspective: 1000px;
}

.box3 {
  position: absolute;
  --strip-size: 400px;
  width: 200px;
  aspect-ratio: 1.5;
  transform: rotate3d(-1, 0, 1, 90deg);
  perspective: 1000px;
}

.face {
  transform-origin: center;
  aspect-ratio: 1;
  border-radius: 100%;
  box-shadow: 35px -35px 20px 2px rgba(33, 94, 151, 0), rgb(177, 208, 239) 12px -11px 7px 11px, rgb(108, 159, 211) 7px -13px 7px 9px, rgb(108, 159, 211) 7px -13px 7px 9px, rgb(3, 139, 206) 7px -13px 7px 9px;
  /*box-shadow: 30px -35px 20px 2px rgb(33, 94, 151, 0),
  rgba(33, 94, 151, 1) 2px -60px 2px 4px;*/
  /* box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px,
    rgba(0, 0, 0, 0.3) 0px 7px 13px -3px,
    rgba(0, 0, 0, 0.2) 0px -3px 0px inset;*/
}

.face.back {
  right: 3px;
  left: -3px;
  transform-origin: center;
  animation: open-2 1s linear infinite;
  animation-delay: -0.5s;
}

/*------------------------------------------------------------------------------*/
.face.left {
  right: 3px;
  left: -3px;
  transform-origin: center;
  animation: open-3 1s linear infinite;
  animation-delay: -0.5s;
}

/*------------------------------------------------------------------------------*/
.face.up {
  right: 3px;
  left: -3px;
  transform-origin: center;
  animation: open-1 1s linear infinite;
  animation-delay: -0.5s;
}

@keyframes open-1 {
  from {
    transform: rotate(180deg);
  }
  to {
    transform: rotate(-180deg);
  }
}
@keyframes open-2 {
  from {
    transform: rotate(-210deg);
  }
  to {
    transform: rotate(150deg);
  }
}
@keyframes open-3 {
  from {
    transform: rotate(-40deg);
  }
  to {
    transform: rotate(320deg);
  }
}
.btn.logo {
  place-items: center;
  background: #e3edf7;
  /*background-image: url('~src/assets/images/tadiranLogo4.svg');*/
  background-repeat: no-repeat;
  background-position: center;
  width: 600px;
  height: 600px;
  padding: 1.4em;
  border-radius: 10px;
  box-shadow: 6px 6px 10px -1px rgba(0, 0, 0, 0.15), -6px -6px 10px -1px rgba(255, 255, 255, 0.7);
  border: 1px solid rgba(0, 0, 0, 0);
  cursor: pointer;
  transition: transform 0.5s;
}

.btn {
  width: 500px;
  height: 500px;
  place-items: center;
  background: #e3edf7;
  /*baground: url("./assets/images/tadiranLogo4.svg");*/
  padding: 1.4em;
  border-radius: 10px;
  box-shadow: 6px 6px 10px -1px rgba(0, 0, 0, 0.15), -6px -6px 10px -1px rgba(255, 255, 255, 0.7);
  border: 1px solid rgba(0, 0, 0, 0);
  cursor: pointer;
  transition: transform 0.5s;
}

.btn:hover {
  box-shadow: inset 4px 4px 6px -1px rgba(0, 0, 0, 0.2), inset -4px -4px 6px -1px rgba(255, 255, 255, 0.7), -0.5px -0.5px 0px rgb(255, 255, 255), 0.5px 0.5px 0px rgba(0, 0, 0, 0.15), 0px 12px 10px -10px rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(0, 0, 0, 0.1);
  transform: translateY(0.5em);
}

.btn box {
  transition: transform 0.5s;
}

.btn:hover box {
  transform: scale(0.9);
  fill: #333333;
}

.sb-intro-logo {
  background: #e8e8e8;
}

h1.loader {
  text-align: center;
  text-transform: uppercase;
  font-family: "Nunito", sans-serif;
  font-size: 4.6875em;
  color: transparent;
  letter-spacing: 0.01em;
}

h1.loader.small {
  text-align: center;
  text-transform: uppercase;
  font-family: "Nunito", sans-serif;
  font-size: 1.1em;
  color: transparent;
  letter-spacing: 0.03em;
}

.loader span {
  text-shadow: 0 0 2px rgb(59, 78, 141), 0 15px 25px rgba(0, 0, 0, 0.3), 0 -2px 3px rgba(0, 0, 0, 0.1), 0 -5px 10px rgba(255, 255, 255, 0.5), 0 5px 10px rgba(0, 0, 0, 0.3), 0 3px 4px rgba(255, 255, 255, 0.2), 0 0 20px rgba(255, 255, 255, 0.45);
  animation: loading 0.85s ease-in-out infinite alternate;
}

@keyframes loading {
  to {
    text-shadow: 0 0 2px rgba(204, 208, 212, 0.2), 0 0 3px rgba(0, 0, 0, 0.02), 0 0 0 rgba(0, 0, 0, 0), 0 0 0 rgba(255, 255, 255, 0), 0 0 0 rgba(0, 0, 0, 0), 0 0 0 rgba(255, 255, 255, 0), 0 0 0 rgba(255, 255, 255, 0);
  }
}
.loader span:nth-child(2) {
  animation-delay: 0.15s;
}

.loader span:nth-child(3) {
  animation-delay: 0.3s;
}

.loader span:nth-child(4) {
  animation-delay: 0.45s;
}

.loader span:nth-child(5) {
  animation-delay: 0.6s;
}

.loader span:nth-child(6) {
  animation-delay: 0.75s;
}

.loader span:nth-child(7) {
  animation-delay: 0.9s;
}